<template>
	<div class="saleedi-order-itemDetails">
		<div class="item-details-mask" @click="maskClickHandler" v-show="maskShow"></div>
		<div class="itemDetails-tools-bar clearfix">
			<div class="class-btn" @click="classBtnHandle">
				<i class="class-btn-icon fl-l"></i>
				<span class="class-btn-text fl-l">分</br>类</span>
			</div>
			<form action="/" class="search-form">
				<van-search
					class="itemDetails-search"
					v-model="searchValue"
					placeholder="明细搜索,请输入搜索关键词"
					@search="onSearch"
				/>
			</form>
			<van-button
				v-show="!billState"
				class="confirm-btn"
				size="small"
				type="primary"
				round
				@click="confirmHandle">
					提交
				</van-button>
			<van-button
				v-show="billState === '1'"
				class="confirm-btn"
				size="small"
				type="primary"
				round
				@click="saveHandle">
					保存
				</van-button>
		</div>
		<itemGrid
			:itemGridData="gridData"
			@itemCountClick="itemCountClickHandle"
			:isActive="rowActive"
			ref="item-grid"
		></itemGrid>
		<div class="itemDetails-bottom" v-show="!numberKeyboardShow">
			<div class="itemDetails-sum-box">
				<div class="sum-box-icon">
					<i class="sum-icon"></i>
				</div>
				<div class="sum-box-count">
					<div>订货明细数：{{itemSumLength}}</div>
					<div>订货品项数量：{{itemSumCount}}</div>
				</div>
				<div class="sum-box-price">{{itemSumPrice}}</div>
			</div>
		</div>
		<van-popup v-model="slideMenu" position="left" class="slide-menu">
			<div class="slideMenu-item">品项分类</div>
			<div
				class="slideMenu-item"
				:class="{'is-active': 'selected' === bigClassActive}"
				@click="bigClassClickHandler('selected')">已选品项</div>
			<div class="slideMenu-item">全部品项</div>
			<div
				class="slideMenu-item"
				v-for="(item, index) in bigClassData"
				:key="index"
				:class="{'is-active': index === bigClassActive}"
				@click="bigClassClickHandler(index)"
			>{{item.name}}</div>
		</van-popup>
		<div v-show="numberKeyboardShow" class="details-number-keyboard">
			<itemNumberKeyboard
				:title="keyboardTitle"
				@input="onKeyboardInput"
				@pre="onKeyboardPre"
				@next="onKeyboardNext"
				@delete="onKeyboardDel"
			></itemNumberKeyboard>
		</div>
	</div>
</template>

<script>
import itemGrid from '@/components/ItemGrid.vue'
import itemNumberKeyboard from '@/components/ItemNumberKeyboard.vue'
// let allClassItemDataJson = require('@/json/itemDetail.json')
export default {
	name: 'orderItemDetails',
	components: {
		itemGrid,
		itemNumberKeyboard
	},
	data () {
		return {
			billId: this.$route.query.id,
			billState: this.$route.query.billState,
			searchValue: '',
			bigClassActive: '',
			allClassItemData: [],
			gridData: [],
			bigClassData: [],
			currentSelectItem: [],
			slideMenu: false,
			numberKeyboardShow: false,
			keyboardTitle: '',
			rowActive: '',
			maskShow: false
		}
	},
	created () {
		if (this.billState === '1') {
			this.getBillAndDetails().then(res => {
				this.initAllClassItemData(res)
			})
		} else {
			this.getOrderPlanDetail().then(res => {
				this.initAllClassItemData(res)
			})
		}
		// this.initAllClassItemData(allClassItemDataJson.data)
	},
	computed: {
		itemSumLength () {
			let itemArr = this.getCurrentSelectItem()
			return itemArr.length
		},
		itemSumCount () {
			let itemArr = this.getCurrentSelectItem()
			let itemSumCount = itemArr.reduce((pre, cur) => {
				return pre + (cur.orderAmount - 0)
			}, 0)
			return itemSumCount
		},
		itemSumPrice () {
			let sumPrice = ''
			let itemArr = this.getCurrentSelectItem()
			sumPrice = itemArr.reduce((pre, cur) => {
				let price = parseFloat(pre) + (parseFloat(cur.priceNoTax) * parseFloat(cur.orderAmount))
				return price
			}, 0)
			return this.$fxUtils.toFixedSHW(sumPrice, 2)
		}
	},
	watch: {
		bigClassActive (val) {
			if (val === 'selected') {
				this.currentSelectItem = this.getCurrentSelectItem()
				this.gridData = this.currentSelectItem
			} else {
				this.gridData = this.allClassItemData[val].detailDTOS
			}
		},
		rowActive (val) {
			if (val !== '') {
				let row = this.gridData[val]
				this.keyboardTitle = `${row.goodsName}(${row.unitName})`
			}
		}
	},
	methods: {
		getOrderPlanDetail () {
			return new Promise(resolve => {
				const params = {
					companyName: this.$route.query.companyName,
					planID: this.$route.query.planID,
					rdcId: this.$route.query.rdcId
				}
				this.$fxHttp.requestPost({
					url: '/getOrderPlanDetail',
					data: params
				}).then(res => {
					resolve(res)
				})
			})
		},
		initAllClassItemData (res) {
			res.forEach(classItem => {
				classItem.detailDTOS.forEach(item => {
					item.planDtId = item.id
					if (!item.orderAmount) {
						item.orderAmount = '0'
					}
				})
			})
			let bigClassData = res.map(item => {
				return {
					id: item.id,
					name: item.name
				}
			})
			if (bigClassData.length > 0) {
				this.bigClassActive = 0
			}
			this.bigClassData = bigClassData
			this.allClassItemData = res
		},
		getBillAndDetails () {
			return new Promise(resolve => {
				const params = {
					id: this.$route.query.id
				}
				this.$fxHttp.requestPost({
					url: '/getBillAndDetails',
					data: params
				}).then(res => {
					this.translateBillData(res.details).then(res => {
						resolve(res)
					})
				})
			})
		},
		translateBillData (details) {
			return new Promise(resolve => {
				let detailsClass = details.map(item => {
					return item.goodsTypeId
				})
				detailsClass = Array.from(new Set(detailsClass))
				let allClassItemData = detailsClass.map(item => {
					let obj = {
						id: item
					}
					let detailDTOS = details.filter(detailsItem => {
						return detailsItem.goodsTypeId === item
					})
					obj.name = detailDTOS[0].goodsName
					obj.detailDTOS = detailDTOS
					return obj
				})
				resolve(allClassItemData)
			})
		},
		confirmHandle () {
			const params = {
				organId: this.$route.query.rdcId,
				organName: this.$route.query.rdcName,
				receiptInfoId: '',
				companyId: this.$route.query.companyId,
				companyName: this.$route.query.companyName,
				planId: this.$route.query.planID,
				details: this.getCurrentSelectItem()
			}
			this.$fxHttp.requestPost({
				url: '/saveBill',
				data: params
			}).then(res => {
				this.$router.push({
					path: '/home/order'
				})
			})
		},
		saveHandle () {
			const params = {
				id: this.$route.query.id,
				organId: this.$route.query.rdcId,
				organName: this.$route.query.rdcName,
				receiptInfoId: this.$route.query.receiptInfoId,
				companyId: this.$route.query.companyId,
				companyName: this.$route.query.companyName,
				planId: this.$route.query.planID,
				details: this.getCurrentSelectItem()
			}
			this.$fxHttp.requestPost({
				url: '/updateBill',
				data: params
			}).then(res => {
				this.$toast.success('保存成功')
			})
		},
		onSearch () {
			console.log('搜索')
		},
		classBtnHandle () {
			this.slideMenu = !this.slideMenu
		},
		itemCountClickHandle ([count, row, index]) {
			this.rowActive = index
			this.numberKeyboardShow = true
			this.maskShow = true
		},
		bigClassClickHandler (index) {
			this.bigClassActive = index
		},
		closeKeyboard () {
			this.rowActive = ''
		},
		onKeyboardInput (val) {
			this.setItemCount(val)
		},
		onKeyboardPre () {
			if (!this.rowActive) {
				return false
			}
			this.rowActive--
			this.$refs['item-grid'].onSelectLetter(this.rowActive, 5)
		},
		onKeyboardNext () {
			if (this.rowActive === '' || this.rowActive === this.gridData.length - 1) {
				return false
			}
			this.rowActive++
			this.$refs['item-grid'].onSelectLetter(this.rowActive, 5)
		},
		setItemCount (val) {
			let orderAmount = this.gridData[this.rowActive].orderAmount + ''
			if (orderAmount.indexOf('.') === -1 && orderAmount.length === 4 && val !== '.') {
				return false
			}
			if (orderAmount.indexOf('.') !== -1 && val === '.') {
				return false
			}
			if (orderAmount.indexOf('.') !== -1 && orderAmount.length === 7) {
				return false
			}
			if (orderAmount === '0') {
				this.gridData[this.rowActive].orderAmount = val
			} else {
				this.gridData[this.rowActive].orderAmount += val
			}
		},
		onKeyboardDel (val) {
			let orderAmount = this.gridData[this.rowActive].orderAmount + ''
			if (orderAmount === '0') {
				return false
			}
			if (orderAmount.length === 1) {
				orderAmount = '0'
			} else {
				orderAmount = orderAmount.substr(0, orderAmount.length - 1)
			}
			this.gridData[this.rowActive].orderAmount = orderAmount
		},
		maskClickHandler () {
			if (this.rowActive === 'selected') {
				this.copyCurrentItemAmount()
			}
			this.maskShow = false
			this.numberKeyboardShow = false
			this.rowActive = ''
		},
		getCurrentSelectItem () {
			let currentSelectItem = this.allClassItemData.reduce((pre, cur) => {
				let selectItem = cur.detailDTOS.filter(item => {
					return parseFloat(item.orderAmount) > 0
				})
				pre = pre.concat(selectItem)
				return pre
			}, [])
			return currentSelectItem
		},
		copyCurrentItemAmount () {
			this.currentSelectItem.forEach(currentItem => {
				this.allClassItemData.forEach(bigClass => {
					bigClass.detailDTOS.forEach(item => {
						if (currentItem.id === item.id) {
							item.orderAmount = currentItem.orderAmount
						}
					})
				})
			})
		}
	}
}
</script>
