<template>
	<div class="itemDetails-grid">
		<div class="grid-title">
			<div class="grid-td-itemName fx-ellipsis">品项名称</div>
			<div class="grid-td-unit">单位</div>
			<div class="grid-td-price">单价（元）</div>
			<div class="grid-td-count">订货数量</div>
			<div class="grid-td-info">备注</div>
		</div>
		<div class="grid-body" ref="grid-body">
			<van-collapse v-model="activeNames" accordion ref="gridBody">
				<van-collapse-item
					v-for="(item, index) in itemGridData"
					:key="index"
					:name="index"
					:ref="`grid-row-${index}`"
					class="grid-collapse-item"
					:border="false"
					:is-link="false">
					<div slot="title" class="grid-tr" :class="{'is-active': isActiveTrClass(index), 'has-amount': hasAmountTrClass(item.orderAmount)}">
						<div class="grid-td-itemName fx-ellipsis" @click.stop>{{item.goodsName}}</div>
						<div class="grid-td-unit" @click.stop>{{item.unitName}}</div>
						<div class="grid-td-price" @click.stop>{{item.priceNoTax | filterPrice}}</div>
						<div class="grid-td-count" @click.stop="itemCountClickHandle(item.orderAmount, item, index)">{{item.orderAmount}}</div>
						<div class="grid-td-info">
							<i class="grid-td-info-icon" :class="{'grid-td-info-icon-active':item.remark !== '' && item.info !== undefined}"></i>
						</div>
					</div>
					<div>
						<van-field
							v-model="item.remark"
							class="itemDetails-textarea"
							type="textarea"
							placeholder="请输入备注信息(最多输入50个字符)"
							rows="3"
							@blur="activeNames = ''"
							/>
					</div>
				</van-collapse-item>
			</van-collapse>
		</div>
	</div>
</template>

<script>
import { scrollTopW } from '@/js/lib/scrollTop'
export default {
	name: 'itemGrid',
	props: {
		itemGridData: {
			type: Array,
			default: () => []
		},
		isActive: [Number, String]
	},
	data () {
		return {
			activeNames: ''
		}
	},
	methods: {
		itemCountClickHandle (count, row, index) {
			this.onSelectLetter(index)
			this.activeNames = ''
			this.$emit('itemCountClick', [count, row, index])
		},
		onSelectLetter (index, distance = 40) {
			let offsetTop = this.$refs[`grid-row-${index}`][0].$el.offsetTop
			let $el = this.$refs['grid-body']
			scrollTopW({ offsetTop, $el, distance })
		},
		hasAmountTrClass (orderAmount) {
			return parseFloat(orderAmount) > 0
		},
		isActiveTrClass (index) {
			return this.isActive === index
		}
	}
}
</script>
