<template>
	<div class="item-number-keyboard">
		<div class="number-keyboard-title">{{title}}</div>
		<div class="number-keyboard-content">
			<div class="keyboard-number">
				<div class="keyboard-number-row">
					<i class="keyboard-btn-1" @click="numberBtnClick('1')">1</i>
					<i class="keyboard-btn-1" @click="numberBtnClick('2')">2</i>
					<i class="keyboard-btn-1" @click="numberBtnClick('3')">3</i>
				</div>
				<div class="keyboard-number-row">
					<i class="keyboard-btn-1" @click="numberBtnClick('4')">4</i>
					<i class="keyboard-btn-1" @click="numberBtnClick('5')">5</i>
					<i class="keyboard-btn-1" @click="numberBtnClick('6')">6</i>
				</div>
				<div class="keyboard-number-row">
					<i class="keyboard-btn-1" @click="numberBtnClick('7')">7</i>
					<i class="keyboard-btn-1" @click="numberBtnClick('8')">8</i>
					<i class="keyboard-btn-1" @click="numberBtnClick('9')">9</i>
				</div>
				<div class="keyboard-number-row">
					<i class="keyboard-btn-2 keyboard-btn-zero" @click="numberBtnClick('0')">0</i>
					<i class="keyboard-btn-1 keyboard-btn-dot" @click="numberBtnClick('.')">.</i>
				</div>
			</div>
			<div class="keyboard-control">
				<i class="keyboard-btn-2 keyboard-btn-del" @click="delBtnClick">x</i>
				<i class="keyboard-btn-3 keyboard-btn-pre" @click="preBtnClick">上一个</i>
				<i class="keyboard-btn-3 keyboard-btn-next" @click="nextBtnClick">下一个</i>
			</div>
		</div>
	</div>
</template>

<script>
export default {
	name: 'itemNumberKeyboard',
	props: {
		title: String
	},
	methods: {
		numberBtnClick (keyValue) {
			this.$emit('input', keyValue)
		},
		delBtnClick () {
			this.$emit('delete')
		},
		preBtnClick () {
			this.$emit('pre')
		},
		nextBtnClick () {
			this.$emit('next')
		}
	}
}
</script>
